import React, { Component } from "react";
import "./index.css";

class Awards extends Component {
  constructor(props) {
    super();
    this.state = {
      certificates: [
        {
          name: "Programming in HTML5 with JavaScript and CSS3",
          code: "70-480",
          conductedBy: "Microsoft",
          score: '881/1000',
          completedOn: "May, 2019",
        },
        {
          name: "Azure Fundamentals",
          code: "AZ-900",
          conductedBy: "Microsoft",
          score: '823/1000',
          completedOn: "February, 2020",
        },
      ],
    };
  }
  componentWillReceiveProps(nextProps) {}
  render() {
    const certifications = this.state.certificates.map((item) => {
      return (
        <li>
          <span className="code">
            {item.name} ({item.code})
          </span>
          <section className="details">
            <div className="coducted">
              <label>Conducted By</label>
              <span>{item.conductedBy}</span>
            </div>
            <div className="score">
              <label>Score</label>
              <span>{item.score}</span>
            </div>
            <div className="completed-on">
              <label>Completed On</label>
              <span>{item.completedOn}</span>
            </div>
          </section>
        </li>
      );
    });
    return (
      <section className="awards even" id={this.props.elId}>
        <div className="wrapper">
          <div className="each-section experience">
            <h2>
              My <span className="signature">Certifications</span>
            </h2>
          </div>
          <ul className="certification-wrapper">{certifications}</ul>
        </div>
      </section>
    );
  }
}

export default Awards;
